.orders{
    background-image: linear-gradient(rgba(8,0,58,0.7),rgba(8,0,58,0.7)), url('../assets/bg.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    min-height: 100vh;
}

/* Table Styling */
.orders-table {
    width: 100%;
    border-collapse: collapse;
    background: rgba(255, 255, 255, 0.1); /* Semi-transparent background */
    border-radius: 8px;
    overflow: hidden;
    font-family: 'Arial', sans-serif;
    text-align: left;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3); /* Add some depth */
}

.orders-table th,
.orders-table td {
    padding: 22px 15px;
    font-size: 1.5rem;
    border: 1px solid rgba(255, 255, 255, 0.3); /* Subtle border matching the background */
}

.orders-table th {
    background-color: rgba(13, 71, 161, 0.9); /* Deep blue with slight transparency */
    color: #ffffff; /* White text for contrast */
    font-weight: bold;
    text-transform: uppercase;
}

.orders-table td {
    color: #ffffff; /* White text for readability */
    background-color: rgba(13, 13, 47, 0.8); /* Darker shade for cells */
}

.orders-table tr:nth-child(even) {
    background-color: rgba(19, 19, 65, 0.8); /* Slightly lighter shade for alternating rows */
}

.orders-table tr:hover {
    background-color: rgba(255, 193, 7, 0.8); /* Highlight row with warm color on hover */
    color: #000; /* Black text for better contrast on hover */
}

.orders-title {
    font-size: 2.8rem;
    color: #ffffff; /* White title for better visibility */
    margin-bottom: 15px;
    text-align: center;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.6); /* Subtle shadow for depth */
}

/* Responsive Design */
@media (max-width: 600px) {
    .orders-table th,
    .orders-table td {
        font-size: 0.9rem;
        padding: 10px;
    }

    .orders-title {
        font-size: 1.5rem;
    }
}
