.food-list {
    display: flex;
    flex-wrap: wrap;
    gap: 90px;
    justify-content: center;
    padding: 40px;
    background-image: linear-gradient(rgba(8,0,58,0.7),rgba(8,0,58,0.7)), url('../assets/bg.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    min-height: 100vh;
  }
  
  .food-card {
    width: 300px;
    border-radius: 58px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
    background-color: #ffffff;
    overflow: hidden;
    text-align: center;
    transition: transform 0.3s ease;
    max-height: fit-content;
  }
  
  .food-card:hover {
    transform: scale(1.10);
  }
  
  .food-image {
    width: 100%;
    height: 180px;
    object-fit: cover;
  }
  
  .food-info {
    padding: 20px;
  }
  
  .food-name {
    font-size: 1.2em;
    color: #333;
  }
  
  .food-expiry {
    color: #300909;
    margin: 5px 4px;
  }
  
  .order-button {
    background-color: #ff6347;
    color: #fff;
    border: none;
    padding: 8px 15px;
    border-radius: 4px;
    cursor: pointer;
    font-size: 1em;
    transition: background-color 0.3s ease;
  }
  
  .order-button:hover {
    background-color: #ff4500;
  }

  .modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
}

.modal {
    background: white;
    padding: 30px;
    border-radius: 10px;
    width: 400px;
    text-align: center;
    font-size: 1.2rem;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.modal-actions {
    margin-top: 20px;
    display: flex;
    justify-content: space-around;
}

.modal-actions button {
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.modal-actions button:first-child {
    background: green;
    color: white;
}

.modal-actions button:last-child {
    background: red;
    color: white;
}

  