/* FoodForm.css */
.main-c{
    background-image: linear-gradient(rgba(8,0,58,0.7),rgba(8,0,58,0.7)), url('../assets/bg.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    min-height: 100vh;
}

.form-container {
    background-color: #fcf7fcb6;
    border-radius: 28px;
    max-width: 400px;
    padding: 40px;
    margin: auto;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    text-align: center;
    font-family: Arial, sans-serif;
  }
  
  h2 {
    color: #333;
    margin-bottom: 20px;
    font-weight: bold;
  }
  
  .form-group {
    margin-bottom: 25px;
    text-align: left;
  }
  
  label {
    display: block;
    color: #150808;
    font-size: 18px;
    margin-bottom: 5px;
  }
  
  input[type="text"],
  input[type="date"],
  input[type="number"] {
    width: 100%;
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
  }
  
  input[type="text"]:focus,
  input[type="date"]:focus,
  input[type="number"]:focus {
    border-color: #1e90ff;
    outline: none;
  }
  
  .submit-button {
    background-color: #390e45;
    color: #fff;
    font-size: 16px;
    padding: 10px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .submit-button:hover {
    background-color: #155a9c;
  }
  